  methods: {
    searchdata() {
      this.chats = {};
      if (this.search == "") {
        this.chats = this.all_chats;
      } else {
        if (Object.keys(this.all_chats).length > 0)
          for (var i = 0; i < Object.keys(this.all_chats).length; i++) {
            let name = this.all_chats[i].name.toLowerCase();
            let result = name.search(this.search.toLowerCase());
            if (!result) {
              this.chats[i] = this.all_chats[i];
            }
          }
      }
    },
    scroller() {
      setTimeout(() => {
        const chat = document.querySelector(".chat_box");
        chat.scrollTop =
          chat.offsetHeight *
          Object.keys(this.all_chats[this.chat_index].messages).length;
      }, 1);
    },
  },
  mounted() {
    this.searchdata();
  },
};
