    clear_arr() {
      let element;
      let element2;
      let i;
      this.rest = 0;
      this.slider_images = {};
      this.slider_position = 1;
      if (this.type_ph == "video") {
        element2 = Object.keys(this.videos);
        if (element2.length > 7) {
          for (i = 1; i <= 7; i++) {
            this.slider_images[i] = this.videos[i];
          }
        } else {
          for (i = 1; i <= element2.length; i++) {
            this.slider_images[i] = this.videos[i];
          }
        }
      } else if (this.type_ph == "photo") {
        element2 = Object.keys(this.images);
        if (element2.length > 7) {
          for (i = 1; i <= 7; i++) {
            this.slider_images[i] = this.images[i];
          }
        } else {
          for (i = 1; i <= element2.length; i++) {
            this.slider_images[i] = this.images[i];
          }
        }
      }
      element = Object.keys(this.slider_images);
      this.slider_length = element.length;
      if (this.slider_length < element2.length) {
        this.rest = element2.length - this.slider_length;
      }
      this.src = this.slider_images[1].src;
    },
    clear_arr2() {
      let i;
      if (this.rest2 > 0) {
        let element;
        this.rest2 = 0;
        element = Object.keys(this.images_update);
        for (i = 4; i < element.length; i++) {
          this.slider_images2[i] = this.images_update[i];
        }
      } else {
        let element;
        let element2;
        this.slider_images2 = {};
        element2 = Object.keys(this.images_update);
        if (element2.length > 4) {
          for (i = 0; i < 4; i++) {
            this.slider_images2[i] = this.images_update[i];
          }
        } else {
          for (i = 0; i < element2.length; i++) {
            this.slider_images2[i] = this.images_update[i];
          }
        }
        element = Object.keys(this.slider_images2);
        this.slider_length2 = element.length;
        if (this.slider_length2 < element2.length) {
          this.rest2 = element2.length - this.slider_length;
        }
      }
    },
    photo_changer(index) {
      if (index == this.slider_length && this.rest > 0) {
        this.rest = 0;
        let element;
        if (this.type_ph == "photo") {
          element = Object.keys(this.images);
          for (let i = 8; i <= element.length; i++) {
            this.slider_images[i] = this.images[i];
          }
        } else {
          element = Object.keys(this.videos);
          for (let i = 8; i <= element.length; i++) {
            this.slider_images[i] = this.videos[i];
          }
        }
      } else {
        this.src = this.slider_images[index].src;
        this.slider_position = index;
      }
