    slide(id) {
      if (id == 1) {
        var el = document.querySelector(".mixed-array-demo  .el-01");
        anime({
          targets: [
            el,
            ".mixed-array-demo  .el-01",
            ".mixed-array-demo  .el-01",
          ],
          keyframes: [
            { translateX: "-200%" },
            { translateY: "-2000%" },
            { translateX: "200%" },
            { translateY: "0%" },
            { translateX: "0%" },
          ],
          duration: 1000,
          easing: "easeInOutQuad",
        });
      }
