    change_img(i) {
      if (i == 1) {
        if (this.slider_position == 1) {
          this.slider_position = Object.keys(this.images).length;
        } else {
          this.slider_position--;
        }
      } else {
        if (this.slider_position == Object.keys(this.images).length) {
          this.slider_position = 1;
        } else {
          this.slider_position++;
        }
      }
      this.src = this.images[this.slider_position].src;
    },
    find_type() {
      var index = 0;
      var page = 0;
      for (var i = 0; i < Object.keys(store.state.types["en"]).length; i++) {
        for (
          var j = 0;
          j < Object.keys(store.state.types["en"][i]).length;
          j++
        ) {
          if (store.state.types["en"][i][j].name == this.real_type) {
            page = i;
            index = j;
            break;
          }
        }
      }
      if (store.state.lang == "en") {
        this.real_type_lang = store.state.types["en"][page][index].name;
      } else if (store.state.lang == "ar") {
        this.real_type_lang = store.state.types["ar"][page][index].name;
      }
    },
    find_city() {
      var index = 0;
      for (var i = 0; i < Object.keys(store.state.citys["en"]).length; i++) {
        if (store.state.citys["en"][i].name == this.location.city) {
          index = i;
          break;
        }
      }
      if (store.state.lang == "en") {
        this.location.city = store.state.citys["en"][index].name;
      } else if (store.state.lang == "ar") {
        this.location.city = store.state.citys["ar"][index].name;
      }
    },
    translateservices() {
      if (Object.keys(this.incoming_services).length > 0) {
        for (var i = 0; i < Object.keys(this.incoming_services).length; i++) {
          for (
            var j = 0;
            j < Object.keys(store.state.all_services[store.state.lang]).length;
            j++
          ) {
            if (
              this.incoming_services[i].id ==
              store.state.all_services[store.state.lang][j].id
            ) {
              this.services[i] = store.state.all_services[store.state.lang][j];
            }
          }
        }
      }
    },
    check3d() {
      if (this.plan3d_src != null) {
        router.push("/3d?id=" + this.id);
      }
    },
