  mounted() {
    this.clear_arr();
    this.pagetype = store.state.pagetype;
    if (this.pagetype == "project_id") {
      this.clear_arr2();
    }
    this.plan_src = this.residential_plans[0].src;
    this.find_type();
    this.find_city();
    this.translateservices();
