<template>
  <div class="Own-realestates">
    <div class="container my-5 p-3">
      <PersonalBar>
        <div
          class="row w-100 bg-light p-5 flex-center"
          ref="target2"
          style="border-radius: 0px 0px 10px 10px; border: none"
        >
          <div
            class="col-12 add-real mb-5 w-100"
            style="height: fit-content; width: 100%"
          >
            <div
              class="w-50 h-100"
              :class="$store.state.lang == 'en' ? 'text-start' : 'text-end'"
            >
              {{
                $store.state.lang == "en"
                  ? "Real estates"
                  : $store.state.lang == "ar"
                  ? "العقارات"
                  : ""
              }}
              ({{ realnum }})
            </div>
            <div
              class="w-50 h-100"
              :class="$store.state.lang == 'en' ? 'text-end' : 'text-start'"
            >
              <button type="button" class="mybtn" @click="addreal()">
                {{
                  $store.state.lang == "en"
                    ? "Add real estate"
                    : $store.state.lang == "ar"
                    ? "إضافة عقار"
                    : ""
                }}
              </button>
            </div>
          </div>
          <div class="col-12" v-if="Object.keys(realestates).length == 0">
            {{
              $store.state.lang == "en"
                ? "No real estates has been added, Please add real estate"
                : $store.state.lang == "ar"
                ? "لم يتم إضافة عقارات بعد، الرجاء إضافة عقار"
                : ""
            }}
          </div>
          <div
            class="col-4 flex-center"
            v-else
            v-for="real in realestates[current_page]"
            :key="real.id"
            dir="ltr"
          >
            <RealestateCard
              :id="real.id"
              :name="real.name"
              :price="real.price"
              :location="real.location"
              :space="real.space"
              :type="real.type"
              :bedrooms="real.bedrooms"
              :bathrooms="real.bathrooms"
              :src="real.src"
              :state="real.state"
              :fav="real.fav"
              :phone="real.phone"
              :email="real.email"
              page_type="real-estate"
              page_type2="dashboard"
              :statics="real.statics"
            ></RealestateCard>
          </div>
          <div class="col-12">
            <div class="row" dir="ltr">
              <div class="col-12">
                <div
                  class="container pagi d-flex justify-contnet-center align-items-center mb-5"
                >
                  <div class="mx-3">
                    <button
                      type="button"
                      class="left-arrow"
                      @click="
                        if (current_page == 1) {
                          current_page = realestates.max_pages;
                        } else {
                          current_page--;
                        }
                        scrollToElement();
                      "
                    >
                      <Icon icon="material-symbols:arrow-forward-ios-rounded" />
                    </button>
                  </div>
                  <span
                    class="mx-3 d-flex justify-content-center align-items-center"
                    v-for="(element, index) in realestates"
                    v-bind:key="index"
                  >
                    <button
                      v-if="element != realestates.max_pages"
                      type="button"
                      class="page_number"
                      :style="{
                        backgroundColor:
                          current_page == index ? '#e57c23' : 'white',
                      }"
                      @click="
                        current_page = index;
                        scrollToElement();
                      "
                    >
                      {{ index }}
                    </button>
                  </span>
                  <div class="mx-3">
                    <button
                      type="button"
                      class="right-arrow"
                      @click="
                        if (current_page == realestates.max_pages) {
                          current_page = 1;
                        } else {
                          current_page++;
                        }
                        scrollToElement();
                      "
                    >
                      <Icon icon="material-symbols:arrow-forward-ios-rounded" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </PersonalBar>
    </div>
  </div>
</template>

<script>
import PersonalBar from "@/components/DashboardBar/PersonalBar.vue";
import router from "@/router";
import RealestateCard from "@/components/RealestateCard.vue";
import { Icon } from "@iconify/vue";

export default {
  name: "own-realestates",
  data() {
    return {
      realnum: 0,
      current_page: 0,
    };
  },
  components: {
    PersonalBar,
    RealestateCard,
    Icon,
  },
  props: {
    realestates: Object,
    mypage: Number,
  },
  methods: {
    addreal() {
      router.push("/add_real");
    },
    scrollToElement() {
      this.$refs.target2.scrollIntoView({
        behavior: "smooth",
      });
    },
    realcount() {
      this.current_page = this.mypage;
      for (let index = 1; index <= this.realestates.max_pages; index++) {
        for (let j = 0; j < Object.keys(this.realestates[index]).length; j++) {
          this.realnum++;
        }
      }
    },
  },
  mounted() {
    this.realcount();
  },
};
</script>

