  border: unset;
  height: 50px;
  border-radius: 10px;
  text-align: center;
}
.pagi {
  background-color: white;
  height: 64px;
  width: max-content;
  border: none;
  border-radius: 15px;
  -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
}

.left-arrow {
  display: flex;
  justify-content: center;
