            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element3.style.display = "none";
          const element4 = document.getElementById("wa_btn");
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      }
    },
    async copy(type) {
      if (type == "phone") {
        const element = document.getElementById("ph_btn");
        navigator.clipboard.writeText(this.phone);
        element.innerHTML =
          store.state.lang == "en"
            ? "Copied"
            : store.state.lang == "ar"
            ? "تم النسخ"
            : "";
        element.style.backgroundColor = "green";
        element.style.color = "white";
      } else {
        const element = document.getElementById("em_btn");
        navigator.clipboard.writeText(this.email);
        element.innerHTML =
          store.state.lang == "en"
            ? "Copied"
            : store.state.lang == "ar"
            ? "تم النسخ"
            : "";
        element.style.backgroundColor = "green";
        element.style.color = "white";
      }
    },
    show_photo() {
      const element2 = document.getElementById("popup2");
      if (element2.style.visibility == "hidden") {
        element2.style.visibility = "visible";
        element2.style.opacity = 1;
      } else {
        element2.style.visibility = "hidden";
        element2.style.opacity = 0;
      }
    },
    show_share() {
      const title = window.document.title;
      const url = window.document.location.href;

      if (navigator.share && window.innerWidth < 500) {
        navigator
          .share({
            title: `${title}`,
            url: `${url}`,
          })
          .then(() => {
            console.log("Thanks for sharing!");
          })
          .catch(console.error);
      } else {
        const element2 = document.getElementById("popup3");
        if (element2.style.visibility == "hidden") {
          element2.style.visibility = "visible";
          element2.style.opacity = 1;
        } else {
          element2.style.visibility = "hidden";
          element2.style.opacity = 0;
        }
      }
    },
    find_city() {
      var index = 0;
      for (var i = 0; i < Object.keys(store.state.citys["en"]).length; i++) {
        if (store.state.citys["en"][i].name == this.location.city) {
          index = i;
          break;
        }
      }
      if (store.state.lang == "en") {
        this.location.city = store.state.citys["en"][index].name;
      } else if (store.state.lang == "ar") {
        this.location.city = store.state.citys["ar"][index].name;
      }
    },
  },
  mounted() {
    this.find_city();
  },
};
</script>

