<template>
  <div class="change-passsword">
    <div
      class="container my-5 p-5 bg-light w-100"
      :dir="$store.state.lang == 'en' ? 'ltr' : 'rtl'"
    >
      <div class="row w-100">
        <div class="col-12 my-4">
          <h2>
            {{
              $store.state.lang == "en"
                ? "Change the password"
                : $store.state.lang == "ar"
                ? "قم بتغيير كلمة المرور"
                : ""
            }}
          </h2>
        </div>
        <div class="col-12 flex-center my-3">
          <div
            class="card w-100"
            style="
              background-color: unset;
              border: none;
              margin: 0px;
              padding: 0px;
            "
          >
            <input
              type="password"
              :placeholder="
                $store.state.lang == 'en'
                  ? 'Your new Password'
                  : $store.state.lang == 'ar'
                  ? 'ادخل كلمة السر الجديدة'
                  : ''
              "
              class="myinput"
              v-model="password"
              id="pass"
            />
            <div
              class="show_hide"
              @click="showpassword(1)"
              :style="{
                right: $store.state.lang == 'en' ? '0px' : '',
                left: $store.state.lang == 'en' ? '' : '0px',
              }"
            >
              <Icon :icon="myicon" :color="mycolor" />
            </div>
          </div>
        </div>
        <div class="col-12 flex-center my-3">
          <div
            class="card w-100"
            style="
              background-color: unset;
              border: none;
              margin: 0px;
              padding: 0px;
            "
          >
            <input
              type="password"
              :placeholder="
                $store.state.lang == 'en'
                  ? 'Check password'
                  : $store.state.lang == 'ar'
                  ? 'التحقق من كلمة المرور'
                  : ''
              "
              class="myinput"
              v-model="chkpassword"
              id="chkpass"
            />
            <div
              class="show_hide"
              @click="showpassword(2)"
              :style="{
                right: $store.state.lang == 'en' ? '0px' : '',
                left: $store.state.lang == 'en' ? '' : '0px',
              }"
            >
              <Icon :icon="myicon2" :color="mycolor2" />
            </div>
          </div>
        </div>
        <div class="col-12 my-3">
          <button type="button" class="mybtn" @click="savepass()">
            {{
              $store.state.lang == "en"
                ? "Save"
                : $store.state.lang == "ar"
                ? "حفظ"
                : ""
            }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import store from "@/store";
import { Icon } from "@iconify/vue";
export default {
  name: "change-passsword",
  data() {
    return {
      password: "",
      chkpassword: "",
      mycolor: "#777777",
      myicon: "gridicons:visible",
      mycolor2: "#777777",
      myicon2: "gridicons:visible",
    };
  },
  components: {
    Icon,
  },
  methods: {
    showpassword(x) {
      if (x == 1) {
        const ele = document.getElementById("pass");
        if (ele.type == "password") {
          ele.type = "text";
          this.myicon = "gridicons:not-visible";
          this.mycolor = "#3c3c3c";
        } else {
          ele.type = "password";
          this.myicon = "gridicons:visible";
          this.mycolor = "#777777";
        }
      } else {
        const ele = document.getElementById("chkpass");
        if (ele.type == "password") {
          ele.type = "text";
          this.myicon2 = "gridicons:not-visible";
          this.mycolor2 = "#3c3c3c";
        } else {
          ele.type = "password";
          this.myicon2 = "gridicons:visible";
          this.mycolor2 = "#777777";
        }
      }
    },
    savepass() {
      router.push("/login?user_type=" + store.state.user_type);
    },
  },
};
</script>

<style scoped>
.myinput {
  width: 100%;
  height: 50px;
  border: 1px solid #aaaaaa;
  padding: 10px;
  border-radius: 10px;
}
.myinput:focus {
  outline: unset !important;
}
.mybtn {
  width: 100%;
  height: 50px;
  border: none;
  background-color: #e57c23;
  color: white;
  font-size: 24px;
  border-radius: 10px;
}
.show_hide {
  cursor: pointer;
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 20px;
  display: flex;
  justify-content: end;
  align-items: center;
}
</style>
