  methods: {
    addpayment() {
      if (this.payment_name == null || this.payment_price == "") {
        this.payment_error =
          store.state.lang == "en"
            ? "You missed something"
            : store.state.lang == "ar"
            ? "نسيت شيءً"
            : "";
        return;
      }
      this.payment_counter++;
      this.payments_arr.push({
        id: this.payment_counter,
        name: this.payment_name,
        type: this.payment_price,
      });
    },
    remove_payment(index) {
      var i = index;
      if (Object.keys(this.payments_arr).length == 1) {
        this.payments_arr.splice(i, 1);
        this.payment_counter--;
        return;
      }
      for (i; i < Object.keys(this.payments_arr).length - 1; i++) {
        this.payments_arr[i] = this.payments_arr[i + 1];
        this.payments_arr[i].id = i + 1;
      }
      this.payments_arr.splice(i, 1);
      this.payment_counter--;
    },
    addfile(type) {
      var input;
      var name;
      if (type == "2D") {
        input = document.querySelector("#plan2d");
        name = this.plan2d;
      } else {
        input = document.querySelector("#plan3d");
        name = this.plan3d;
      }
      const file = input.files[0];
      if (file == null || name == "") {
        this.plan_error =
          store.state.lang == "en"
            ? "You missed something"
            : store.state.lang == "ar"
            ? "نسيت شيءً"
            : "";
        return;
      } else {
        this.plan_error = "";
      }
      this.plans_counter++;
      this.plans2d.push({
        id: this.plans_counter,
        name: name,
        type: type,
        file: file,
      });
    },
    remove_file(index) {
      var i = index;
      if (Object.keys(this.plans2d).length == 1) {
        this.plans2d.splice(i, 1);
        this.plans_counter--;
        return;
      }
      for (i; i < Object.keys(this.plans2d).length - 1; i++) {
        this.plans2d[i] = this.plans2d[i + 1];
        this.plans2d[i].id = i + 1;
      }
      this.plans2d.splice(i, 1);
      this.plans_counter--;
    },
    add360photo() {
      var input = document.querySelector("#photo360");
      const file = input.files[0];
      if (
        file == null ||
        this.photo360_name == "" ||
        this.photo360_type == "Photo type"
      ) {
        this.photo360_error =
          store.state.lang == "en"
            ? "You missed something"
            : store.state.lang == "ar"
            ? "نسيت شيءً"
            : "";
        return;
      } else {
        this.photo360_error = "";
      }
      this.photo360_counter++;
      this.photo360_arr.push({
        id: this.photo360_counter,
        name: this.photo360_name,
        type: this.photo360_type,
        file: file,
      });
    },
    remove_360photo(index) {
      var i = index;
      if (Object.keys(this.photo360_arr).length == 1) {
        this.photo360_arr.splice(i, 1);
        this.photo360_counter--;
        return;
      }
      for (i; i < Object.keys(this.photo360_arr).length - 1; i++) {
        this.photo360_arr[i] = this.photo360_arr[i + 1];
        this.photo360_arr[i].id = i + 1;
      }
      this.photo360_arr.splice(i, 1);
      this.photo360_counter--;
    },
    async initMap() {
      this.loader = new Loader({
        apiKey: "AIzaSyCRm9FZWayulRn3b4fv3R3GZE3ZPGAti78",
        version: "weekly", // Specify the desired version of the Google Maps API
        libraries: ["places"],
      });
      this.loader.load().then(() => {
        // auto complete
        const autocomplete = new window.google.maps.places.Autocomplete(
          document.getElementById("autocompleteInput")
        );
        this.autocomplete = autocomplete;
        autocomplete.addListener("place_changed", () => {
          const place = autocomplete.getPlace();
          if (place.geometry && place.geometry.location) {
            this.error = "";
            const lat = place.geometry.location.lat();
            const lng = place.geometry.location.lng();

            // Do something with the latitude and longitude values
            this.map.setCenter({ lat, lng });
            this.map.setZoom(15);
          } else {
            this.error =
              store.state.lang == "en"
                ? "You should choose the place from the autocomplete menu"
                : store.state.lang == "ar"
                ? "يجب عليك إختيار المكان من القائمة"
                : "";
          }
        });

        // The Google Maps API has been loaded
        // You can now use the map and markers
        this.map = new window.google.maps.Map(document.getElementById("map"), {
          center: { lat: 33.2232, lng: 43.6793 },
          zoom: 7,
        });

        let markers = []; // Array to store the markers

        // Function to add new markers based on zoom level
        const addMarker = (latLng) => {
          if (markers.length == 1) {
            // Marker already exists, remove it
            markers[0].setMap(null);
            markers = [];
          }
          // Marker does not exist, create it
          const marker = new window.google.maps.Marker({
            position: latLng,
            map: this.map,
            icon: {
              url: customMarkerImage, // Set the custom marker image URL
              scaledSize: new window.google.maps.Size(35, 35), // Set the desired size of the marker image
            },
          });
          this.lating = { lat: latLng.lat(), lng: latLng.lng() };
          markers.push(marker);
        };

        // Add event listener for zoom change
        this.map.addListener("click", (event) => {
          addMarker(event.latLng);
        });
      });
    },
    add_remove_service(id) {
      const index = this.added_services.findIndex((ele) => ele.id === id);
      if (index > -1) {
        this.added_services.splice(index, 1);
      } else {
        this.added_services.push({ id });
      }
    },
    show_more() {
      var i;
      if (this.service_flag == 0) {
        this.service_flag = 1;
        for (i = 0; i < 9; i++) {
          this.services[i] = this.All_services[store.state.lang][i];
        }
      } else {
        for (
          i = 9;
          i < Object.keys(this.All_services[store.state.lang]).length;
          i++
        ) {
          this.services[i] = this.All_services[store.state.lang][i];
        }
        const element = document.getElementById("sh_mr");
        element.style.opacity = 0;
      }
    },
    show_message() {
      const element = document.getElementById("popup");
      element.style.opacity = 1;
      element.style.visibility = "visible";
    },
    exit() {
      const element = document.getElementById("popup");
      element.style.opacity = 0;
      element.style.visibility = "hidden";
      router.push("/my_projects");
    },
    showinfo() {
      const element = document.getElementById("popup2");
      element.style.opacity = 1;
      element.style.visibility = "visible";
    },
    exit_info() {
      const element = document.getElementById("popup2");
      element.style.opacity = 0;
      element.style.visibility = "hidden";
    },
  },
  mounted() {
    this.initMap();
    this.show_more();
  },
};
