  methods: {
    add_time() {
      for (var i = 0; i < Object.keys(this.working_hours_arr).length; i++) {
        if (
          this.working_hours_arr[i].day == this.working_day &&
          this.working_hours_arr[i].start_at == this.time_start &&
          this.working_hours_arr[i].end_at == this.time_end
        ) {
          this.error_message =
            store.state.lang == "en"
              ? "You already have this time in your list"
              : store.state.lang == "ar"
              ? "لديك هذا التوقيت بالفعل"
              : "";
          return;
        }
      }
      if (
        this.working_day == "" ||
        this.time_start == "" ||
        this.time_end == ""
      ) {
        this.error_message =
          store.state.lang == "en"
            ? "You missed a field"
            : store.state.lang == "ar"
            ? "نسيت حقل"
            : "";
        return;
      }
      if (this.time_start == this.time_end) {
        this.error_message =
          store.state.lang == "en"
            ? "Start time and end time should not be the same"
            : store.state.lang == "ar"
            ? "لا يجب ان يكون وقت البدء والإنتهاء متماثلين"
            : "";
        return;
      }
      this.error_message = "";
      this.id_counter++;
      this.working_hours_arr[this.id_counter - 1] = {
        id: this.id_counter,
        day: this.working_day,
        start_at: this.time_start,
        end_at: this.time_end,
        holiday: this.holiday,
      };
    },
    remove_time(index) {
      var i = index;
      if (Object.keys(this.working_hours_arr).length == 1) {
        delete this.working_hours_arr[i];
        this.id_counter--;
        return;
      }
      for (i; i < Object.keys(this.working_hours_arr).length - 1; i++) {
        this.working_hours_arr[i] = this.working_hours_arr[i + 1];
        this.working_hours_arr[i].id = i + 1;
      }
      delete this.working_hours_arr[i];
      this.id_counter--;
    },
    async initMap() {
      this.loader = new Loader({
        apiKey: "AIzaSyCRm9FZWayulRn3b4fv3R3GZE3ZPGAti78",
        version: "weekly", // Specify the desired version of the Google Maps API
        libraries: ["places"],
      });
      this.loader.load().then(() => {
        // auto complete
        const autocomplete = new window.google.maps.places.Autocomplete(
          document.getElementById("autocompleteInput")
        );
        this.autocomplete = autocomplete;
        autocomplete.addListener("place_changed", () => {
          const place = autocomplete.getPlace();
          if (place.geometry && place.geometry.location) {
            this.error = "";
            const lat = place.geometry.location.lat();
            const lng = place.geometry.location.lng();

            // Do something with the latitude and longitude values
            this.map.setCenter({ lat, lng });
            this.map.setZoom(15);
          } else {
            this.error =
              store.state.lang == "en"
                ? "You should choose the place from the autocomplete menu"
                : store.state.lang == "ar"
                ? "يجب عليك إختيار المكان من قائمة الإقتراحات"
                : "";
          }
        });

        // The Google Maps API has been loaded
        // You can now use the map and markers
        this.map = new window.google.maps.Map(document.getElementById("map"), {
          center: { lat: 33.2232, lng: 43.6793 },
          zoom: 7,
        });

        let markers = []; // Array to store the markers

        // Function to add new markers based on zoom level
        const addMarker = (latLng) => {
          if (markers.length == 1) {
            // Marker already exists, remove it
            markers[0].setMap(null);
            markers = [];
          }
          // Marker does not exist, create it
          const marker = new window.google.maps.Marker({
            position: latLng,
            map: this.map,
            icon: {
              url: customMarkerImage, // Set the custom marker image URL
              scaledSize: new window.google.maps.Size(35, 35), // Set the desired size of the marker image
            },
          });
          this.lating = { lat: latLng.lat(), lng: latLng.lng() };
          markers.push(marker);
        };

        // Add event listener for zoom change
        this.map.addListener("click", (event) => {
          addMarker(event.latLng);
        });
      });
    },
  },
  mounted() {
    this.initMap();
  },
