<template>
  <div class="city-search">
    <div class="dropdown open">
      <button
        class="form-home1 form-home2"
        type="button"
        id="triggerId"
        data-bs-toggle="dropdown"
        aria-haspopup="true"
        aria-expanded="false"
      >
        <Icon
          icon="zondicons:location"
          color="#585858"
          :height="page_type == 'home' ? '25px' : ''"
          :width="page_type == 'home' ? '25px' : ''"
        />
        {{ selected_city }}
        <Icon
          :icon="icon1"
          color="#585858"
          :height="page_type == 'home' ? '25px' : ''"
          :width="page_type == 'home' ? '25px' : ''"
        />
      </button>
      <div
        class="dropdown-menu"
        :class="
          $store.state.lang == 'en'
            ? 'dropdown-menu-start'
            : 'dropdown-menu-end'
        "
        aria-labelledby="triggerId"
        style="overflow-y: scroll; height: 150px; width: fit-content"
      >
        <button
          v-for="element in $store.state.citys[$store.state.lang]"
          :key="element.id"
          type="button"
          class="dropdown-item w-100 text-center"
          :style="{
            backgroundColor: 'unset',
            border: 'none',
            color: selected_city == element.name ? '#e57c23' : 'black',
          }"
          @click="
            selected_city = element.name;
            $store.state.selected_city = element.id;
          "
        >
          {{ element.name }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";
import store from "@/store";
export default {
  name: "city-search",
  data() {
    return {
      icon1: "fe:arrow-down",
      city_id: null,
      selected_city: "",
    };
  },
  components: {
    Icon,
  },
  props: {
    page_type: String,
  },
  methods: {
    changecity(x, id) {
      this.city = x;
      this.city_id = id;
    },
    changic1(x) {
      switch (x) {
        case 1:
          if (this.icon1 == "fe:arrow-down") {
            this.icon1 = "fe:arrow-up";
          } else {
            this.icon1 = "fe:arrow-down";
          }
          break;
      }
    },
  },
  mounted() {
    if (store.state.lang == "en") {
      this.selected_city = "City";
    } else if (store.state.lang == "ar") {
      this.selected_city = "المدينة";
    } else {
      this.selected_city = "";
    }
  },
};
</script>

