<script>
import Navbar from "./components/Navbar.vue";
import { Icon } from "@iconify/vue";
import store from "./store";
import router from "./router";
export default {
  name: "MyApp",
  components: { Navbar, Icon },
  data() {
    return {
      screen_width: 0,
      languages: {
        en: {
          0: {
            lang: "English",
            imgsrc: require("@/assets/images/flags/USA.png"),
            short: "en",
          },
          1: {
            lang: "Arabic",
            imgsrc: require("@/assets/images/flags/iraq.png"),
            short: "ar",
          },
          2: {
            lang: "Kurdish",
            imgsrc: require("@/assets/images/flags/kurdish.png"),
            short: "kr",
          },
        },
        ar: {
          0: {
            lang: "الإنجليزية",
            imgsrc: require("@/assets/images/flags/USA.png"),
            short: "en",
          },
          1: {
            lang: "العربية",
            imgsrc: require("@/assets/images/flags/iraq.png"),
            short: "ar",
          },
          2: {
            lang: "الكردية",
            imgsrc: require("@/assets/images/flags/kurdish.png"),
            short: "ar",
          },
        },
      },
      exp_message: {
        en: "Explore the world of real estate in Iraq",
        ar: "إستكشف عالم العقارات في العراق",
      },
      lan: "",
      imgsrc: "",
      currency: "$USD",
      src: require("@/assets/logo2.png"),
      fb: require("@/assets/images/Social media/fb.png"),
      insta: require("@/assets/images/Social media/Instagram.png"),
      snap: require("@/assets/images/Social media/Snapchat.png"),
      twitter: require("@/assets/images/Social media/twitter x.png"),
      prevroute: "",
      password: "",
    };
  },
  methods: {
    change_lang(lang) {
      store.state.lang = lang;
      var r = document.querySelector(":root");
      switch (store.state.lang) {
        case "en":
          this.lan = "English";
          this.imgsrc = require("@/assets/images/flags/USA.png");
          r.style.setProperty("--font", "expoenglish");
          r.style.setProperty("--rtop", "4px");
          r.style.setProperty("--rleft", "4px");
          break;
        case "ar":
          this.lan = "العربية";
          this.imgsrc = require("@/assets/images/flags/iraq.png");
          r.style.setProperty("--font", "expoarabic");
          r.style.setProperty("--rtop", "4px");
          r.style.setProperty("--rleft", "22px");
          break;
        case "kr":
          this.lan = "الكردية";
          this.imgsrc = require("@/assets/images/flags/kurdish.png");
          r.style.setProperty("--font", "expoarabic");
          r.style.setProperty("--rtop", "4px");
          r.style.setProperty("--rleft", "22px");
          break;
      }
      window.localStorage.setItem("lang", store.state.lang);
    },
    reload_page(lang) {
      window.localStorage.setItem("lang", lang);
      window.location.reload();
    },
    scrollToElement() {
      this.$refs.target.scrollIntoView({
        top: 0,
        behavior: "instant",
      });
    },
    go() {
      router.push("/login-as");
    },
    unlock() {
      if (this.password == "2711") {
        const element2 = document.getElementById("popup-global");
        if (element2.style.visibility == "hidden") {
          element2.style.visibility = "visible";
          element2.style.opacity = 1;
        } else {
          element2.style.visibility = "hidden";
          element2.style.opacity = 0;
          document.body.style.overflowY = "visible";
        }
      }
    },
  },
  watch: {
    $route(to, from) {
      this.scrollToElement();
      console.log(to, from);
      store.state.prevroute = from.href;
      if (from.path == "/360-photos" && to.path != "/360-photos") {
        location.reload();
      }
      if ("project_id" in to.query && "company_id" in to.query) {
        store.state.company_flag_crew = 1;
      } else {
        store.state.company_flag_crew = 0;
      }
      if ("real_id" in to.query) {
        store.state.real_id = to.query.real_id;
        store.state.pagetype = "real_id";
      } else if ("agent_id" in to.query) {
        store.state.agent_id = to.query.agent_id;
      } else if ("company_id" in to.query) {
        store.state.company_id = to.query.company_id;
      } else if ("service_company_id" in to.query) {
        store.state.service_company_id = to.query.service_company_id;
      } else if ("project_id" in to.query) {
        store.state.project_id = to.query.project_id;
        store.state.pagetype = "project_id";
      } else if ("user_type" in to.query) {
        store.state.user_type = to.query.user_type;
        window.localStorage.setItem("user_type", store.state.user_type);
      }
    },
  },
  mounted() {
    document.body.style.overflowY = "hidden";
    store.state.user_type = window.localStorage.getItem("user_type");
    if (window.localStorage.getItem("lang") !== null) {
      store.state.lang = window.localStorage.getItem("lang");
    } else {
      store.state.lang = "en";
      window.localStorage.setItem("lang", store.state.lang);
    }
    switch (store.state.lang) {
      case "en":
        this.change_lang("en");
        break;
      case "ar":
        this.change_lang("ar");
        break;
      case "kr":
        this.change_lang("en");
        break;
    }
    this.screen_width = screen.width;
    store.state.screen_width = this.screen_width;
  },
};
</script>

