<template>
  <div class="findon-map">
    <div class="card" style="background-color: unset">
      <div class="row">
        <div class="col-12 flex-center" style="position: relative; z-index: 1">
          <div class="card w-50" style="position: absolute; top: 0">
            <div class="row">
              <div class="col-12 flex-center">
                <div
                  class="error p-3 text-start w-75 flex-center"
                  v-if="error != ''"
                >
                  {{ error }}
                </div>
              </div>
              <div class="col-12 flex-center">
                <div class="row p-4 w-100">
                  <div class="col-10 flex-center">
                    <input
                      type="text"
                      class="w-100 myinp"
                      id="autocompleteInput"
                    />
                  </div>
                  <div class="col-2 flex-center">
                    <button
                      type="button"
                      class="flex-center mybtn"
                      @click="
                        map.setCenter(mycenter);
                        map.setZoom(7);
                      "
                    >
                      <Icon icon="mdi:location" color="#ffffff" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-12" style="padding-bottom: 600px">
          <div class="map" id="map"></div>
        </div>
      </div>
    </div>
    <div id="popup2" class="overlay" style="visibility: hidden; opacity: 0">
      <div class="popup">
        <button type="button" class="close" @click="show_photo()">
          <Icon
            icon="fluent-emoji-high-contrast:cross-mark"
            height="20"
            width="20"
          />
        </button>
        <div
          class="content mt-5 d-flex justify-content-center align-items-center"
          v-if="Object.keys(latest_offer).length != 0"
        >
          <RealestateCardVue
            :id="latest_offer.id"
            :name="latest_offer.name"
            :price="latest_offer.price"
            :location="latest_offer.location"
            :space="latest_offer.space"
            :type="latest_offer.type"
            :bedrooms="latest_offer.bedrooms"
            :bathrooms="latest_offer.bathrooms"
            :src="latest_offer.src"
            :logo="latest_offer.logo"
            :state="latest_offer.state"
            :fav="latest_offer.fav"
          ></RealestateCardVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";
import { Loader } from "@googlemaps/js-api-loader";
import customMarkerImage from "@/assets/location.png";
import { MarkerWithLabel } from "@googlemaps/markerwithlabel";
import RealestateCardVue from "@/components/RealestateCard.vue";

export default {
  name: "findon-map",
  data() {
    return {
      address: "",
      center: {}, // Set initial map center
      // zoom: 10, // Set initial zoom level
      mycenter: { lat: 33.2232, lng: 43.6793 },
      markers: {
        0: {
          position: { lat: 33.2232, lng: 43.6793 },
          id: 1,
          name: "Real_estate",
          src: require("@/assets/images/Exterior flat/Exterior.jpg"),
          price: "60,000$",
          location: { id: 2, city: "Erbil", place: "central park" },
          space: "156m2",
          type: "sell",
          bedrooms: "4",
          bathrooms: "2",
          state: "old",
          fav: "1",
          logo: require("../assets/images/3aa11431-e9c0-4417-b1b2-c51fdf1b8084.jpg"),
        },
        1: {
          position: { lat: 32.2232, lng: 42.6793 },
          id: 2,
          name: "Real_estate 2",
          src: require("@/assets/images/Exterior flat/Exterior.jpg"),
          price: "60,000$",
          location: { id: 2, city: "Erbil", place: "central park" },
          space: "156m2",
          type: "rent",
          bedrooms: "4",
          bathrooms: "2",
          state: "new",
          fav: "0",
          logo: require("../assets/images/3aa11431-e9c0-4417-b1b2-c51fdf1b8084.jpg"),
        },
        2: {
          position: { lat: 31.2232, lng: 41.6793 },
          id: 3,
          name: "Real_estate 3",
          src: require("@/assets/images/Exterior flat/Exterior.jpg"),
          price: "60,000$",
          location: { id: 2, city: "Erbil", place: "central park" },
          space: "156m2",
          type: "sell",
          bedrooms: "4",
          bathrooms: "2",
          state: "old",
          fav: "1",
          logo: require("../assets/images/3aa11431-e9c0-4417-b1b2-c51fdf1b8084.jpg"),
        },
        3: {
          position: { lat: 34.2232, lng: 44.6793 },
          id: 4,
          name: "Real_estate 4",
          src: require("@/assets/images/Exterior flat/Exterior.jpg"),
          price: "60,000$",
          location: { id: 2, city: "Erbil", place: "central park" },
          space: "156m2",
          type: "sell",
          bedrooms: "4",
          bathrooms: "2",
          state: "old",
          fav: "1",
          logo: require("../assets/images/3aa11431-e9c0-4417-b1b2-c51fdf1b8084.jpg"),
        },
        // Add more markers as needed
      },
      latest_offer: {},
      error: "",
      autocomplete: null,
      map: null,
      loader: null,
      markers2: [],
    };
  },
  components: {
    Icon,
    RealestateCardVue,
  },
  methods: {
    async initMap() {
      this.loader = new Loader({
        apiKey: "AIzaSyCRm9FZWayulRn3b4fv3R3GZE3ZPGAti78",
        version: "weekly", // Specify the desired version of the Google Maps API
        libraries: ["places"],
      });
      this.loader.load().then(() => {
        // auto complete
        const autocomplete = new window.google.maps.places.Autocomplete(
          document.getElementById("autocompleteInput")
        );
        this.autocomplete = autocomplete;
        autocomplete.addListener("place_changed", () => {
          const place = autocomplete.getPlace();
          if (place.geometry && place.geometry.location) {
            this.error = "";
            const lat = place.geometry.location.lat();
            const lng = place.geometry.location.lng();

            // Do something with the latitude and longitude values
            this.map.setCenter({ lat, lng });
          } else {
            this.error =
              "You should choose the place from the autocomplete menu";
          }
        });

        // The Google Maps API has been loaded
        // You can now use the map and markers
        this.map = new window.google.maps.Map(document.getElementById("map"), {
          center: { lat: 33.2232, lng: 43.6793 },
          zoom: 7,
        });

        let markers = []; // Array to store the markers

        // Function to remove all markers from the map
        const removeMarkers = () => {
          markers.forEach((marker) => {
            // Check if the marker has a label
            if (marker.label) {
              // Remove the label from the marker
              const element = document.getElementById("label1");
              element.remove();
              marker.label.setMap(null);
            }

            // Remove the marker from the map
            marker.setMap(null);
          });

          markers = [];
        };

        // Function to add new markers based on zoom level
        const addMarkers = () => {
          const currentZoom = this.map.getZoom();

          // Remove old markers
          removeMarkers();

          if (currentZoom < 7) {
            // Add new markers for zoom level less than 10
            const marker = new MarkerWithLabel({
              position: { lat: 33.2232, lng: 43.6793 },
              map: this.map,
              icon: {
                url: "", // Set the custom marker image URL
                scaledSize: new window.google.maps.Size(35, 35), // Set the desired size of the marker image
              },
              labelContent:
                "<div id='label1' style='display: flex; justify-content:center; align-items:center; color: white; background-color:#e57c23; border:2px solid white; border-radius:15px; width: 50px; font-weight:800; font-size:24px; margin-left:-10px;'>" +
                Object.keys(this.markers).length +
                "</div>", // Set the content of the label
            });
            markers.push(marker);
          } else {
            for (let i = 0; i < Object.keys(this.markers).length; i++) {
              const marker = new window.google.maps.Marker({
                position: this.markers[i].position,
                map: this.map,
                icon: {
                  url: customMarkerImage, // Set the custom marker image URL
                  scaledSize: new window.google.maps.Size(35, 35), // Set the desired size of the marker image
                },
              });
              marker.addListener("click", () => {
                // Perform actions when the marker is clicked
                this.latest_offer = this.markers[i];
                this.show_photo();
              });
              markers.push(marker);
            }
          }
        };

        // Add event listener for zoom change
        window.google.maps.event.addListener(this.map, "zoom_changed", () => {
          addMarkers();
        });

        // Add initial markers
        addMarkers();
      });
    },
    show_photo() {
      const element2 = document.getElementById("popup2");
      if (element2.style.visibility == "hidden") {
        element2.style.visibility = "visible";
        element2.style.opacity = 1;
      } else {
        element2.style.visibility = "hidden";
        element2.style.opacity = 0;
      }
    },
  },
  mounted() {
    this.initMap();
  },
};
</script>

