<template>
  <div class="login-view">
    <div class="row w-100 p-5">
      <div
        class="col-4 flex-center bg-light"
        style="border-radius: 15px 0px 0px 15px"
        :dir="$store.state.lang == 'en' ? 'ltr' : 'rtl'"
      >
        <div class="card w-100">
          <img
            :src="
              $store.state.lang == 'en'
                ? require('@/assets/images/Sign in/Login english.png')
                : $store.state.lang == 'ar'
                ? require('@/assets/images/Sign in/Login arabic.png')
                : ''
            "
            alt=""
            class="side-img"
          />
          <div class="flex-center abs-p">
            <img
              :src="
                require('@/assets/images/logos/Arabic Tagline ( White )/logo2.png')
              "
              class="logo"
              alt=""
            />
            <div class="flex-center abs-p">
              <span class="mytext">{{ Text1[$store.state.lang] }}</span>
            </div>
          </div>
        </div>
      </div>
      <div
        class="col-8 flex-center bg-light"
        style="border-radius: 0px 15px 15px 0px"
        :dir="$store.state.lang == 'en' ? 'ltr' : 'rtl'"
      >
        <div class="row w-100 p-4 flex-center">
          <div class="col-12 flex-center my-5">
            <h2>
              {{
                $store.state.lang == "en"
                  ? "Login"
                  : $store.state.lang == "ar"
                  ? "تسجيل الدخول"
                  : ""
              }}
            </h2>
          </div>
          <div class="col-12 flex-center my-3">
            <input
              type="text"
              :placeholder="
                $store.state.lang == 'en'
                  ? 'Enter your username'
                  : $store.state.lang == 'ar'
                  ? 'ادخل اسم المستخدم'
                  : ''
              "
              class="myinp"
              v-model="email"
            />
          </div>
          <div class="col-12 flex-center my-3">
            <div
              class="card w-100"
              style="
                background-color: unset;
                border: none;
                margin: 0px;
                padding: 0px;
              "
            >
              <input
                type="password"
                :placeholder="
                  $store.state.lang == 'en'
                    ? 'Enter your password'
                    : $store.state.lang == 'ar'
                    ? 'ادخل كلمة المرور'
                    : ''
                "
                class="myinp"
                v-model="password"
                id="pass"
              />
              <div
                class="show_hide"
                @click="showpassword()"
                :style="{
                  right: $store.state.lang == 'en' ? '0px' : '',
                  left: $store.state.lang == 'en' ? '' : '0px',
                }"
              >
                <Icon :icon="myicon" :color="mycolor" />
              </div>
            </div>
          </div>
          <div
            class="col-6 d-flex justify-content-start align-items-center my-2"
          >
            <span class="w-100 d-flex justify-content-start align-items-center">
              <label class="container_checkbox">
                <input
                  type="checkbox"
                  @click="
                    remember_me == 0 ? (remember_me = 1) : (remember_me = 0)
                  "
                />
                <span style="color: #2f2f2f">{{
                  $store.state.lang == "en"
                    ? "Remember me"
                    : $store.state.lang == "ar"
                    ? "تذكرني"
                    : ""
                }}</span>
                <span class="checkmark"></span>
              </label>
            </span>
          </div>
          <div class="col-6 d-flex justify-content-end align-items-center my-2">
            <router-link
              to="/forgot-password"
              style="color: #e57c23; text-decoration: none"
            >
              {{
                $store.state.lang == "en"
                  ? "Did you forget your password?"
                  : $store.state.lang == "ar"
                  ? "نسيت كلمة المرور؟"
                  : ""
              }}
            </router-link>
          </div>
          <div class="col-12 flex-center my-3">
            <button type="button" class="mybtn" @click="login()">
              {{
                $store.state.lang == "en"
                  ? "Login"
                  : $store.state.lang == "ar"
                  ? "تسجيل الدخول"
                  : ""
              }}
            </button>
          </div>
          <div class="col-12 flex-center my-3">
            {{
              $store.state.lang == "en"
                ? "Dont you have account"
                : $store.state.lang == "ar"
                ? "ليس لديك حساب؟"
                : ""
            }}<router-link
              :to="'/register?user_type=' + $store.state.user_type"
              style="color: #e57c23; text-decoration: none"
              >{{
                $store.state.lang == "en"
                  ? "Create account"
                  : $store.state.lang == "ar"
                  ? "إنشاء حساب"
                  : ""
              }}</router-link
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";
import store from "@/store";
import router from "@/router";

export default {
  name: "login-view",
  data() {
    return {
      Text1: {
        en: "Welcome to Betonna, the ideal destination for selling and renting real estate in Iraq. Explore our wide range of diverse options, and get a chance to get your dream home. We are here to fulfill your real estate desires.",
        ar: "مرحبًا بك في Betoona، الوجهة الأمثل لبيع وايجار العقارات في العراق. إحصل على فرصة للعثور على منزل أحلامك. نحن هنا لتحقيق رغباتك العقارية.",
      },
      email: "",
      password: "",
      mycolor: "#777777",
      myicon: "gridicons:visible",
      remember_me: 0,
    };
  },
  components: {
    Icon,
  },
  methods: {
    showpassword() {
      const ele = document.getElementById("pass");
      if (ele.type == "password") {
        ele.type = "text";
        this.myicon = "gridicons:not-visible";
        this.mycolor = "#3c3c3c";
      } else {
        ele.type = "password";
        this.myicon = "gridicons:visible";
        this.mycolor = "#777777";
      }
    },
    login() {
      if (store.state.user_type == "personal") {
        router.push("/personal-account");
      }
      if (store.state.user_type == "agent") {
        router.push("/agent_account");
      }
    },
  },
};
</script>

