                  {{ this.phone }}
                </div>
                <div class="d-flex justify-content-center align-items-center">
                  <a
                    class="call_email2 whats_app"
                    :href="'https://wa.me/' + this.phone"
                    target="_blank"
                    :id="'wa_btn' + this.new_id"
                    >{{
                      $store.state.lang == "en"
                        ? "Go to whatsapp"
                        : "الذهاب لواتساب"
