<script>
import { Icon } from "@iconify/vue";
import Chart from "chart.js/auto";
import store from "@/store";

export default {
  name: "realestate-card",
  data() {
    return {
      new_id: 0,
      icon: "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love",
      icon2: "noto:heart-suit",
      copy_btn: { en: "Copy", ar: "نسخ" },
      lang: "",
      location_static: {},
      lang_location: {},
    };
  },
  components: {
    Icon,
  },
  props: {
    id: Number,
    name: String,
    price: String,
    location: Object,
    space: String,
    type: String,
    bedrooms: String,
    bathrooms: String,
    src: String,
    logo: String,
    state: String,
    fav: String,
    page_type: String,
    page_type2: String,
    page_type3: String,
    phone: String,
    email: String,
    company_id: Number,
    price_from: String,
    price_to: String,
    project_state: String,
    statics: Object,
  },
  methods: {
    async change_icon(num) {
      if (num == 1) {
        if (
          this.icon ==
          "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love"
        ) {
          this.icon = "noto:heart-suit";
        } else {
          this.icon =
            "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love";
        }
      } else {
        if (
          this.icon2 ==
          "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love"
        ) {
          this.icon2 = "noto:heart-suit";
        } else {
          this.icon2 =
            "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love";
        }
      }
    },
    async show_hide_statics() {
      const element = document.getElementById("popup");
      if (element.style.visibility == "hidden") {
        element.style.visibility = "visible";
        element.style.opacity = 1;
      } else {
        element.style.visibility = "hidden";
        element.style.opacity = 0;
      }
    },
    async show_hide(type, id) {
      const element = document.getElementById("phone" + id);
      const element2 = document.getElementById("email" + id);
      const element3 = document.getElementById("whatsapp" + id);
      if (type == "phone") {
        if (element.style.display == "none") {
          element.style.display = "block";
          const element4 = document.getElementById("ph_btn" + id);
          element4.focus();
          element2.style.display = "none";
          const element5 = document.getElementById("em_btn" + id);
          element5.innerHTML = this.copy_btn[this.lang];
          element5.style.backgroundColor = "white";
          element5.style.color = "black";
          const element6 = document.getElementById("wa_btn" + id);
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element.style.display = "none";
          const element4 = document.getElementById("ph_btn" + id);
          element4.innerHTML = this.copy_btn[this.lang];
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      } else if (type == "email") {
        if (element2.style.display == "none") {
          element2.style.display = "block";
          element.style.display = "none";
          const element4 = document.getElementById("em_btn" + id);
          element4.focus();
          const element5 = document.getElementById("ph_btn" + id);
          element5.innerHTML = this.copy_btn[this.lang];
          element5.style.backgroundColor = "white";
          element5.style.color = "black";
          const element6 = document.getElementById("wa_btn" + id);
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element2.style.display = "none";
          const element4 = document.getElementById("em_btn" + id);
          element4.innerHTML = this.copy_btn[this.lang];
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      } else if (type == "whatsapp") {
        if (element3.style.display == "none") {
          element3.style.display = "block";
          element.style.display = "none";
          const element4 = document.getElementById("wa_btn" + id);
          element4.focus();
          const element5 = document.getElementById("ph_btn" + id);
          element5.innerHTML = this.copy_btn[this.lang];
          element5.style.backgroundColor = "white";
          element5.style.color = "black";
          const element6 = document.getElementById("em_btn" + id);
          element6.innerHTML = this.copy_btn[this.lang];
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element3.style.display = "none";
          const element4 = document.getElementById("wa_btn" + id);
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      }
    },
    async copy(type) {
      if (type == "phone") {
        const element = document.getElementById("ph_btn" + this.new_id);
        navigator.clipboard.writeText(this.phone);
        element.innerHTML =
          store.state.lang == "en"
            ? "Copied"
            : store.state.lang == "ar"
            ? "تم النسخ"
            : "";
        element.style.backgroundColor = "green";
        element.style.color = "white";
      } else {
        const element = document.getElementById("em_btn" + this.new_id);
        navigator.clipboard.writeText(this.email);
        element.innerHTML =
          store.state.lang == "en"
            ? "Copied"
            : store.state.lang == "ar"
            ? "تم النسخ"
            : "";
        element.style.backgroundColor = "green";
        element.style.color = "white";
      }
    },
    find_city() {
      var index = 0;
      for (var i = 0; i < Object.keys(store.state.citys["en"]).length; i++) {
        if (store.state.citys["en"][i].id == this.location_static.id) {
          index = i;
          break;
        }
      }
      if (store.state.lang == "en") {
        this.location_static.city = store.state.citys["en"][index].name;
      } else if (store.state.lang == "ar") {
        this.location_static.city = store.state.citys["ar"][index].name;
      }
    },
  },
  mounted() {
    this.location_static = this.location;
    this.lang_location = this.location;
    this.lang = store.state.lang;
    this.find_city();
    if (this.page_type == "real-estate") {
      this.new_id = this.id;
      // const element = document.querySelector("card");
      // element.style.width = "310px";
    }
    if (this.page_type2 == "dashboard") {
      if (Chart.getChart("myChart")) {
        var existingChart = Chart.getChart("myChart");
        existingChart.destroy();
      }
      const ctx = document.getElementById("myChart");
      const mixedChart = new Chart(ctx, {
        data: {
          datasets: [
            {
              type: "line",
              label: "Views",
              data: this.statics.views,
              borderColor: "black",
              color: "black",
            },
            {
              type: "line",
              label: "Shares",
              data: this.statics.shares,
              borderColor: "#e57c23",
              color: "#e57c23",
            },
          ],
          labels: [
            "January",
            "February",
            "March",
            "April",
            "May",
            "June",
            "July",
            "August",
            "September",
            "October",
            "November",
            "December",
          ],
        },
        options: {
          responsive: true,
          interaction: {
            mode: "index",
            intersect: false,
          },
          stacked: false,
          plugins: {
            title: {
              display: true,
              text: "Real estate statics",
            },
          },
          scales: {
            y: {
              type: "linear",
              display: true,
              position: "left",
              grid: {
                drawOnChartArea: false, // only want the grid lines for one axis to show up
              },
            },
          },
        },
      });
      mixedChart;
    }
  },
};
</script>

