<template>
  <div class="price-search">
    <button
      class="form-home1 form-home2"
      type="button"
      data-bs-toggle="dropdown"
      aria-expanded="false"
      @click="changic1(4)"
      @blur="changic1(4)"
    >
      <Icon
        icon="mdi:dollar"
        color="#585858"
        :height="page_type == 'home' ? '25px' : ''"
        :width="page_type == 'home' ? '25px' : ''"
      />
      {{
        $store.state.lang == "en"
          ? "Price"
          : $store.state.lang == "ar"
          ? "السعر"
          : ""
      }}
      <Icon
        :icon="icon4"
        color="#585858"
        :height="page_type == 'home' ? '25px' : ''"
        :width="page_type == 'home' ? '25px' : ''"
      />
    </button>
    <ul class="dropdown-menu price-drop">
      <div class="dropdown-item w-100">
        <div class="row w-100 flex-center" style="direction: ltr">
          <div class="col-6 flex-center">
            {{
              $store.state.lang == "en"
                ? "Minimum price"
                : $store.state.lang == "ar"
                ? "السعر الأدنى"
                : ""
            }}
          </div>
          <div class="col-6 flex-center">
            {{
              $store.state.lang == "en"
                ? "Maximum price"
                : $store.state.lang == "ar"
                ? "السعر الأعلى"
                : ""
            }}
          </div>
        </div>
      </div>
      <div class="dropdown-item w-100">
        <div class="row w-100 flex-center" style="direction: ltr">
          <div class="col-5 number-wrapper2">
            <input
              type="number"
              class="list-inp"
              min="0"
              v-model="minimum_price"
              style="background-color: #dddddd"
            /><Icon
              icon="mdi:dollar"
              color="#585858"
              style="margin-left: -40px; z-index: 2; pointer-events: none"
            />
          </div>
          <div class="col-1">
            <Icon
              icon="solar:arrow-right-linear"
              color="black"
              height="16"
              width="16"
            />
          </div>
          <div class="col-5 number-wrapper2">
            <input
              type="number"
              class="list-inp"
              min="0"
              v-model="maximum_price"
              style="background-color: #dddddd"
            /><Icon
              icon="mdi:dollar"
              color="#585858"
              style="margin-left: -40px; z-index: 2; pointer-events: none"
            />
          </div>
        </div>
      </div>
    </ul>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";
export default {
  name: "price-search",
  data() {
    return {
      icon4: "fe:arrow-down",
      minimum_price: "",
      maximum_price: "",
    };
  },
  components: {
    Icon,
  },
  props: {
    page_type: String,
  },
  methods: {
    changic1(x) {
      switch (x) {
        case 4:
          if (this.icon4 == "fe:arrow-down") {
            this.icon4 = "fe:arrow-up";
          } else {
            this.icon4 = "fe:arrow-down";
          }
          break;
      }
    },
  },
};
</script>

<style scoped>
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
