        >
          {{
            $store.state.lang == "en"
              ? "Phone"
              : $store.state.lang == "ar"
              ? "الرقم"
              : ""
          }}:
          <div style="direction: ltr">
            {{ this.phone }}
          </div>
          <button
            class="call_email2"
            :id="'ph_btn' + this.new_id"
            @click.stop="copy('phone')"
          >
            {{ copy_btn[lang] }}
          </button>
        </div>
        <div
          class="container mt-5"
