  methods: {
    async change_icon(num) {
      if (num == 1) {
        if (
          this.icon ==
          "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love"
        ) {
          this.icon = "noto:heart-suit";
        } else {
          this.icon =
            "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love";
        }
      } else {
        if (
          this.icon2 ==
          "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love"
        ) {
          this.icon2 = "noto:heart-suit";
        } else {
          this.icon2 =
            "streamline:interface-favorite-heart-reward-social-rating-media-heart-it-like-favorite-love";
        }
      }
    },
    async show_hide(type) {
      const element = document.getElementById("phone");
      const element2 = document.getElementById("email");
      const element3 = document.getElementById("whatsapp");
      if (type == "phone") {
        if (element.style.display == "none") {
          element.style.display = "block";
          const element4 = document.getElementById("ph_btn");
          element4.focus();
          element2.style.display = "none";
          const element5 = document.getElementById("em_btn");
          element5.innerHTML =
            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element5.style.backgroundColor = "white";
          element5.style.color = "black";
          const element6 = document.getElementById("wa_btn");
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element.style.display = "none";
          const element4 = document.getElementById("ph_btn");
          element4.innerHTML =
            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      } else if (type == "email") {
        if (element2.style.display == "none") {
          element2.style.display = "block";
          element.style.display = "none";
          const element4 = document.getElementById("em_btn");
          element4.focus();
          const element5 = document.getElementById("ph_btn");
          element5.innerHTML =
            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element5.style.backgroundColor = "white";
          element5.style.color = "black";
          const element6 = document.getElementById("wa_btn");
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element2.style.display = "none";
          const element4 = document.getElementById("em_btn");
          element4.innerHTML =
            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      } else if (type == "whatsapp") {
        if (element3.style.display == "none") {
          element3.style.display = "block";
          element.style.display = "none";
          const element4 = document.getElementById("wa_btn");
          element4.focus();
          const element5 = document.getElementById("ph_btn");
          element5.innerHTML =
            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element5.style.backgroundColor = "white";
          element5.style.color = "black";
          const element6 = document.getElementById("em_btn");
          element6.innerHTML =
            store.state.lang == "en"
              ? "Copy"
              : store.state.lang == "ar"
              ? "نسخ"
              : "";
          element6.style.backgroundColor = "white";
          element6.style.color = "black";
        } else {
          element3.style.display = "none";
          const element4 = document.getElementById("wa_btn");
          element4.style.backgroundColor = "White";
          element4.style.color = "black";
        }
      }
    },
    async copy(type) {
      if (type == "phone") {
        const element = document.getElementById("ph_btn");
        navigator.clipboard.writeText(this.phone);
        element.innerHTML =
          store.state.lang == "en"
            ? "Copied"
            : store.state.lang == "ar"
            ? "تم النسخ"
            : "";
        element.style.backgroundColor = "green";
        element.style.color = "white";
      } else {
        const element = document.getElementById("em_btn");
        navigator.clipboard.writeText(this.email);
        element.innerHTML =
          store.state.lang == "en"
            ? "Copied"
            : store.state.lang == "ar"
            ? "تم النسخ"
            : "";
        element.style.backgroundColor = "green";
        element.style.color = "white";
      }
    },
    show_photo() {
      const element2 = document.getElementById("popup2");
      if (element2.style.visibility == "hidden") {
        element2.style.visibility = "visible";
        element2.style.opacity = 1;
      } else {
        element2.style.visibility = "hidden";
        element2.style.opacity = 0;
      }
    },
    show_share() {
      const title = window.document.title;
      const url = window.document.location.href;

      if (navigator.share && window.innerWidth < 500) {
        navigator
          .share({
            title: `${title}`,
            url: `${url}`,
          })
          .then(() => {
            console.log("Thanks for sharing!");
          })
          .catch(console.error);
      } else {
        const element2 = document.getElementById("popup3");
        if (element2.style.visibility == "hidden") {
          element2.style.visibility = "visible";
          element2.style.opacity = 1;
        } else {
          element2.style.visibility = "hidden";
          element2.style.opacity = 0;
        }
      }
    },
    clear_arr() {
      let element;
      let element2;
      let i;
      this.rest = 0;
      this.slider_images = {};
      this.slider_position = 1;
      if (this.type_ph == "video") {
        element2 = Object.keys(this.videos);
        if (element2.length > 7) {
          for (i = 1; i <= 7; i++) {
            this.slider_images[i] = this.videos[i];
          }
        } else {
          for (i = 1; i <= element2.length; i++) {
            this.slider_images[i] = this.videos[i];
          }
        }
      } else if (this.type_ph == "photo") {
        element2 = Object.keys(this.images);
        if (element2.length > 7) {
          for (i = 1; i <= 7; i++) {
            this.slider_images[i] = this.images[i];
          }
        } else {
          for (i = 1; i <= element2.length; i++) {
            this.slider_images[i] = this.images[i];
          }
        }
      }
      element = Object.keys(this.slider_images);
      this.slider_length = element.length;
      if (this.slider_length < element2.length) {
        this.rest = element2.length - this.slider_length;
      }
      this.src = this.slider_images[1].src;
    },
    photo_changer(index) {
      if (index == this.slider_length && this.rest > 0) {
        this.rest = 0;
        let element;
        if (this.type_ph == "photo") {
          element = Object.keys(this.images);
          for (let i = 8; i <= element.length; i++) {
            this.slider_images[i] = this.images[i];
          }
        } else {
          element = Object.keys(this.videos);
          for (let i = 8; i <= element.length; i++) {
            this.slider_images[i] = this.videos[i];
          }
        }
      } else {
        this.src = this.slider_images[index].src;
        this.slider_position = index;
      }
    },
    change_img(i) {
      if (i == 1) {
        if (this.slider_position == 1) {
          this.slider_position = Object.keys(this.images).length;
        } else {
          this.slider_position--;
        }
      } else {
        if (this.slider_position == Object.keys(this.images).length) {
          this.slider_position = 1;
        } else {
          this.slider_position++;
        }
      }
      this.src = this.images[this.slider_position].src;
    },
    enlargeImg() {
      // Set image size to 1.5 times original
      const element = document.getElementById("popimg");
      if (this.flag == 0) {
        this.flag = 1;
        element.style.transform = "scale(2.5)";
        // Animation effect
        element.style.transition = "transform 0.25s ease";
      } else {
        // Set image size to original
        this.flag = 0;
        element.style.transform = "scale(1)";
        element.style.transition = "transform 0.25s ease";
      }
    },
    find_city() {
      var index = 0;
      for (var i = 0; i < Object.keys(store.state.citys["en"]).length; i++) {
        if (store.state.citys["en"][i].name == this.location.city) {
          index = i;
          break;
        }
      }
      if (store.state.lang == "en") {
        this.location.city = store.state.citys["en"][index].name;
      } else if (store.state.lang == "ar") {
        this.location.city = store.state.citys["ar"][index].name;
      }
    },
  },
  mounted() {
    this.clear_arr();
    this.find_city();
  },
};
</script>

