          this.color2 = "#e57c23";
          this.color3 = "black";
          break;
        case 3:
          this.page = x;
          this.color1 = "black";
          this.color2 = "black";
          this.color3 = "#e57c23";
          break;
      }
    },
    choose_type(id, name) {
      const index = this.choosen_type.findIndex((med) => med.name === name);
      if (index > -1) {
        this.choosen_type.splice(index, 1);
        this.types[store.state.lang][this.page][id].color = "black";
      } else {
        this.choosen_type.push({ name });
        this.types[store.state.lang][this.page][id].color = "#e57c23";
      }
    },
    changic1(x) {
      switch (x) {
        case 3:
          if (this.icon3 == "fe:arrow-down") {
            this.icon3 = "fe:arrow-up";
          } else {
            this.icon3 = "fe:arrow-down";
          }
          break;
