<template>
  <div class="realestates-search">
    <form>
      <div
        class="row my-4"
        :style="{ direction: $store.state.lang == 'en' ? 'ltr' : 'rtl' }"
      >
        <div
          class="col-2 d-flex justify-content-center align-items-center place"
          :class="$store.state.lang == 'en' ? 'ms-5 me-4' : 'ms-4 me-5'"
        >
          <Icon icon="material-symbols:location-on-rounded" color="black" />{{
            $store.state.lang == "en"
              ? "Iraq"
              : $store.state.lang == "ar"
              ? "العراق"
              : ""
          }}
        </div>
        <div
          class="col-9 search-line d-flex justify-content-center align-items-center"
        >
          <div
            class="container d-flex justify-content-center align-items-center w-100"
          >
            <div class="row w-100">
              <div class="my-3 col-2">
                <div class="dropdown font-size w-100">
                  <button
                    class="form-home1 form-home2 text-center"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                    @click="changic1(2)"
                    @blur="changic1(2)"
                  >
                    {{
                      search_type == "Sell"
                        ? $store.state.lang == "en"
                          ? "Sell"
                          : $store.state.lang == "ar"
                          ? "بيع"
                          : ""
                        : $store.state.lang == "en"
                        ? "Rent"
                        : $store.state.lang == "ar"
                        ? "إيجار"
                        : ""
                    }}
                    <Icon :icon="icon2" color="#585858" />
                  </button>
                  <ul class="dropdown-menu">
                    <li class="d-flex justify-content-center">
                      <button
                        type="button"
                        class="city-btn"
                        @click="
                          search_type = 'Sell';
                          changecolortype(1);
                        "
                        :style="{ color: search_type_color1 }"
                      >
                        {{
                          $store.state.lang == "en"
                            ? "Sell"
                            : $store.state.lang == "ar"
                            ? "بيع"
                            : ""
                        }}
                      </button>
                    </li>
                    <li><hr class="dropdown-divider" /></li>
                    <li class="d-flex justify-content-center">
                      <button
                        type="button"
                        class="city-btn"
                        @click="
                          search_type = 'Rent';
                          changecolortype(2);
                        "
                        :style="{ color: search_type_color2 }"
                      >
                        {{
                          $store.state.lang == "en"
                            ? "Rent"
                            : $store.state.lang == "ar"
                            ? "إيجار"
                            : ""
                        }}
                      </button>
                    </li>
                    <li><hr class="dropdown-divider" /></li>
                  </ul>
                </div>
              </div>
              <div class="my-3 col-3">
                <div class="dropdown font-size w-100">
                  <CitySearch></CitySearch>
                </div>
              </div>
              <div class="my-3 col-3">
                <div class="dropdown font-size w-100">
                  <PlaceSearch></PlaceSearch>
                </div>
              </div>
              <div class="my-3 col-3">
                <div class="dropdown font-size w-100">
                  <TypesComponent></TypesComponent>
                </div>
              </div>
              <div class="my-3 col-3">
                <div class="dropdown font-size w-100">
                  <PriceSearch></PriceSearch>
                </div>
              </div>
              <div class="my-3 col-3" v-if="search_type == 'Rent'">
                <div class="dropdown font-size w-100">
                  <button
                    class="form-home1 form-home2"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                    @click="changic1(6)"
                    @blur="changic1(6)"
                  >
                    <Icon
                      icon="mdi:sign-real-estate"
                      color="#585858"
                      width="24"
                      height="25"
                    />
                    {{
                      $store.state.lang == "en"
                        ? rent_type
                        : $store.state.lang == "ar"
                        ? rent_type == "Daily"
                          ? "يومي"
                          : rent_type == "Weekly"
                          ? "إسبوعي"
                          : rent_type == "Monthly"
                          ? "شهري"
                          : rent_type == "Yearly"
                          ? "سنوي"
                          : "نوع الإيجار"
                        : ""
                    }}
                    <Icon
                      :icon="icon6"
                      color="#585858"
                      width="25"
                      height="25"
                    />
                  </button>
                  <ul
                    class="dropdown-menu"
                    :class="
                      $store.state.lang == 'en'
                        ? 'dropdown-menu-start'
                        : 'dropdown-menu-end'
                    "
                  >
                    <div
                      v-for="(rent, index) in renting[$store.state.lang]"
                      v-bind:key="rent.id"
                    >
                      <li class="d-flex justify-content-center">
                        <button
                          type="button"
                          class="city-btn"
                          :id="rent.id"
                          :style="{
                            color: rent.id === rent_id ? '#e57c23' : 'black',
                          }"
                          @click="
                            rent_type = renting['en'][index].name;
                            rent_id = rent.id;
                          "
                        >
                          {{ rent.name }}
                        </button>
                      </li>
                      <li><hr class="dropdown-divider" /></li>
                    </div>
                  </ul>
                </div>
              </div>
              <div class="my-3 col-2">
                <div class="d-flex justify-content-center align-items-center">
                  <button type="button" class="search">
                    {{
                      $store.state.lang == "en"
                        ? "Search"
                        : $store.state.lang == "ar"
                        ? "بحث"
                        : ""
                    }}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";
import TypesComponent from "./TypesComponent.vue";
import CitySearch from "./CitySearch.vue";
import PlaceSearch from "./PlaceSearch.vue";
import PriceSearch from "./PriceSearch.vue";
export default {
  name: "realestates-search",
  data() {
    return {
      icon2: "fe:arrow-down",
      icon6: "fe:arrow-down",
      search_type: "Sell",
      rent_type: "Renting type",
      rent_id: 0,
      renting: {
        en: {
          1: { id: 1, name: "Daily" },
          2: { id: 2, name: "Weekly" },
          3: { id: 3, name: "Monthly" },
          4: { id: 4, name: "Yearly" },
        },
        ar: {
          1: { id: 1, name: "يومي" },
          2: { id: 2, name: "إسبوعي" },
          3: { id: 3, name: "شهري" },
          4: { id: 4, name: "سنوي" },
        },
      },
      search_type_color1: "#E57C23",
      search_type_color2: "#000000",
    };
  },
  components: {
    Icon,
    TypesComponent,
    CitySearch,
    PlaceSearch,
    PriceSearch,
  },
  methods: {
    changic1(x) {
      switch (x) {
        case 2:
          if (this.icon2 == "fe:arrow-down") {
            this.icon2 = "fe:arrow-up";
          } else {
            this.icon2 = "fe:arrow-down";
          }
          break;
        case 6:
          if (this.icon6 == "fe:arrow-down") {
            this.icon6 = "fe:arrow-up";
          } else {
            this.icon6 = "fe:arrow-down";
          }
          break;
      }
    },
    changecolortype(x) {
      if (x == 1) {
        this.search_type_color1 = "#E57C23";
        this.search_type_color2 = "#000000";
      } else {
        this.search_type_color1 = "#000000";
        this.search_type_color2 = "#E57C23";
      }
    },
  },
};
</script>

